

body {
  font-family: 'Trebuchet MS';
  font-size: 16px;
}

/* === карточка === */

.foodItem {
  display: flex;
  flex-direction: column;
  background: #fff;
  height: 471px;
  width: 312px;
  overflow: hidden;
  box-sizing: border-box;
  text-align: left;
  border-radius: 5px;
  clip-path: polygon(12% 0, 100% 0, 100% 100%, 0 100%, 0% 8%);
  -webkit-clip-path: polygon(12% 0, 100% 0, 100% 100%, 0 100%, 0% 8%);
}

.wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 12px;
}

.border {
filter:
drop-shadow(0px 0px 0px #1698d9) drop-shadow(-4px 0px 0px #1698d9) drop-shadow(0px 2px 0px #1698d9) drop-shadow(0px 0px 0px #1698d9) drop-shadow(0px 2px 0px #1698d9) drop-shadow(-2px 0px 0px #1698d9) drop-shadow(0px 2px 0px #1698d9) drop-shadow(4px -4px 0px #1698d9);

  margin: 5px;
  border-radius: 8px;
  overflow: hidden;
}

.border:hover {
  filter:
drop-shadow(0px 0px 0px #d91667) drop-shadow(-4px 0px 0px #d91667) drop-shadow(0px 2px 0px #d91667) drop-shadow(0px 0px 0px #d91667) drop-shadow(0px 2px 0px #d91667) drop-shadow(-2px 0px 0px #d91667) drop-shadow(0px 2px 0px #d91667) drop-shadow(4px -4px 0px #d91667);
}



.description {
  padding: 17px 45px;
  position: relative;
  overflow: hidden;
  height: 100%;
}

/* === характеристики карточки === */
.titleFood {
  font-size: 1rem;
  color: #666666;
}

.nameBrand {
  font-size: 3rem;
  color: #000000;
  font-weight: 700;
  line-height: 0.65;
  margin: 1rem 0;
  letter-spacing: 1px;
}

.taste {
  font-size: 1.5rem;
  color: #000000;
  font-weight: bold;
}


.portion,
.present {
  font-size: 0.8rem;
  line-height: 1.1rem;
  color: #666666;
  font-weight: bold;
  margin: 0px;
  letter-spacing: 1.1px;
}
.portion {
  margin-top: 1.35rem;
  
}

.img {
  position: absolute;
    bottom: -88px;
    left: -24px;
  pointer-events: none;
}

.weight {
  font-size: 42px;
  line-height: 22px;
  color: #ffffff;
  font-family: "Trebuchet MS";
  text-align: center;
  border-radius: 50%;
  background-color: #1698d9;
  width: 5rem;
  height: 5rem;
  line-height: 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content:  flex-end;
  position: absolute;
  bottom: 16px;
  right: 17px;
  z-index: 10;
}

.border:hover .weight {
    background-color: #d91667;
}

.weightUnit {
  font-size: 21px;
}

.subscrible {
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  text-align: center;
  margin-top: 14px;
}

.link {
  color: #1698d9;
  text-decoration: none;
  border-bottom: 1px dashed #1698d9;
}